.bubbles{stroke-width:1px;stroke:darkslategray;opacity:0.6}.bubbles:hover{stroke:red;stroke-width:2px;opacity:1;transition:color, 1s;transition:opacity, 1s}text{color:black}.button-container{display:flex;align-items:center;flex-direction:column;justify-content:center;width:20%;position:absolute;right:0;top:200px}body{display:flex}.danceability,.instrumentalness,.valence,.speechness,.acousticness,.liveness{padding:5px;margin-left:5px;font-family:Verdana}button{border:none;width:120px;height:40px;background:gray;color:white;margin:5px;padding:3px;border-radius:10px}button:focus{outline:none}button:hover{cursor:pointer;transition:background-color, 1.5s;background-color:#0a0909}.description{font-family:Verdana;width:370px;font-size:12px}.desc-label{font-family:Verdana;font-weight:600}
